import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			// openid
			openid: null,
			// 手机号码
			phoneNumber: null,
			// 线路特色富文本的内容
			textVal: '<h1>这是富文本内容</h1>',
			// 暂时的临时数据 后期去掉
			id: '',
		}
	},
	methods: {
		// 判断是否登录了 登录了下单 未去登录页面
		getLogin() {
			uni.login({
				provider: 'weixin',
				success: (res) => {
					var code = res.code
					this.$http.post('/api/WeiXinApplet.ashx', {
						action: "Login",
						code: code
					}).then(res => {
						console.log(res)
						if (res.data.status == 'true') {
							uni.setStorageSync('openid', res.data.openid)
							uni.setStorageSync('session_key', res.data.session_key)
							this.getUser()
						} else {
							uni.showToast({
								title: '获取信息异常，请稍后重新操作！',
								duration: 2000,
								icon: 'none'
							});
						}
					})
				}
			});
		},
		// 获取用户信息
		getUser() {
			uni.getUserInfo({
				provider: 'weixin',
				success: (res) => {
					console.log(res)
					if (res.errMsg == 'getUserInfo:ok') {
						uni.setStorageSync('avatarUrl', res.userInfo.avatarUrl)
						uni.setStorageSync('nickName', res.userInfo.nickName)
						uni.navigateTo({
							url: '/pages/login/login'
						});
					}
				},
				fail: err => {
					console.log('没有授权')
				}
			});
		},
		// 打开弹出框
		open() {
			// 如果手机号码为空 打开弹框
			if (this.phoneNumber == '') {
				this.$refs.popup.open()
			} else {
				// 去选择出行时间 / 人数页面
				uni.navigateTo({
					url: '/pages/choice/choice'
				});
			}
		},
		// 去登录页面
		goLogin() {
			uni.navigateTo({
				url: '/pages/login/login'
			});
		},
		// 拨打号码
		goMakePhoneCall() {
			uni.makePhoneCall({
				phoneNumber: '17855355076' //仅为示例
			});
		}
	},
	computed: {
		isOpenid() {
			this.phoneNumber = uni.getStorageSync('phoneNumber')
			this.openid = uni.getStorageSync('openid')
			if (this.openid == '') {
				console.log('没有 openid')
				return true
			}
			console.log('有 openid')
			return false
		}
	},
	onLoad() {}
}
